     },
     mounted() {
     // Agrega un listener para detectar cambios en la media query
     const mediaQuery = window.matchMedia("( max-width: 768px)");
     this.handleMediaQuery(mediaQuery);
     mediaQuery.addListener(this.handleMediaQuery);
     },
     computed: {
       estadisticas() {
         if (this.$route.params.eleccion === 'D') {
           return this.$store.state.dEstadisticasNivelEntidad;
         } if (this.$route.params.eleccion === 'A') {
           return this.$store.state.aEstadisticasNivelEntidad;
         }
         return this.$store.state.gEstadisticasNivelEntidad;
       },
       obtenerRutaDetalle() {    
          if (this.$router.currentRoute.name === 'VotosPorCandidaturas') {
            return `/ENT/VPC`;
          } 
          if (this.$router.currentRoute.name === 'VotosPorPartidoPolitico') {
            return `/ENT/VPP`;
          } 
          if (this.$router.currentRoute.name === 'DetalleDelDistrito') {
            return `/DET/VPC/`;
          } 
          if (this.$router.currentRoute.name === 'DetalleDelDistrito') {
            return `/DET/VPP/`;
          } 
        
          return '';
        },
       resumenVotosXCandidatura() {
        if (this.$route.params.eleccion === 'D') {
            return this.$store.state.dResumenNivelDistrito.distritos[this.$route.params.distrito - 1];
          } if (this.$route.params.eleccion === 'A') {
            return this.$store.state.aResumenNivelDistrito.distritos[this.$route.params.distrito - 1];
          }
          return {};
        },
        resumenVotosXPP(){
          if (this.$route.params.eleccion === 'D') {
              return this.$store.state.dResumenNivelDistrito.distritos[this.$route.params.distrito - 1];
            } if (this.$route.params.eleccion === 'A') {
              return this.$store.state.aResumenNivelDistrito.distritos[this.$route.params.distrito - 1];
            }
            return {};
        },
       colorEsperadas() {
         if (this.$store.state.esOscuro) {
           return 'fd2a71';
         }
         return 'C791A8';
       },
       colorCapturadas() {
         if (this.$store.state.esOscuro) {
           return 'fe9db0';
         }
         return 'B283B9';
       },
       colorContabilizadas() {
         if (this.$store.state.esOscuro) {
           return 'fec5cd';
         }
         return '582D73';
       },
       colorUrbano() {
         if (this.$store.state.esOscuro) {
           return 'F1B1D7';
         }
         return 'd2127e';
       },
       colorNoUrbano() {
         if (this.$store.state.esOscuro) {
           return 'E97FBD';
         }
         return '79144c';
       },
       styleLetter() {
         if (this.$store.state.esOscuro) {
           return 'color:black';
         }
         return 'color:white';
       },
       styleUrbano() {
         if (this.$store.state.esOscuro) {
           return 'background-color: #F1B1D7 !important; color: black !important;';
         }
         return 'background-color: #C791A8 !important;';
       },
       styleNoUrbano() {
         if (this.$store.state.esOscuro) {
           return 'background-color: #E97FBD !important; color: black !important;';
         }
         return 'background-color: #582D73 !important;'; 
       },
       eleccion() {
         return this.$route.params.eleccion;
       },
     },
     methods: {
       handleMediaQuery(mediaQuery) {
       this.isMobile = mediaQuery.matches;
     },
       obtenerAvance(avance) {
         // console.log(avance);
         return Math.ceil(parseFloat(avance));
       },
     },
     watch: {
       eleccion: {
         handler(val, oldVal) {
           if (val !== oldVal) {
             this.randomKey = Math.random();
           }
         },
       },
     },
   };
